export default async function ProjectDetailPage({ params }) {
  const { slug } = await params;

  const project = getProjectBySlug(slug);
  if (!project) return <div>Project Not Found</div>;

  return (
    <>
      <Header />

      <main className="max-w-7xl mx-auto mt-20 md:mt-24 px-4 py-12 md:py-16">
        {/* Category */}
        <p className="text-xs md:text-sm text-[#4D4DFF] font-medium uppercase tracking-widest mb-2">
          Category: {project.category}
        </p>

        {/* Title */}
        <h1 className="text-3xl md:text-5xl font-semibold text-[#1A1A1A] mb-6 md:mb-8">
          {project.title}
        </h1>

        {/* Hero Image */}
        <div className="mb-10 md:mb-12 relative w-full h-[40vh] md:h-[50vh] rounded-xl overflow-hidden shadow-xl">
          <Image
            src={project.images[0]}
            alt={`${project.title} main view`}
            fill
            className="object-cover"
          />
        </div>

        {/* Short Description */}
        <p className="text-base md:text-lg text-[#333333] leading-relaxed mb-12 max-w-4xl">
          {project.description}
        </p>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-10">
          {/* Left Content */}
          <div className="lg:col-span-2">
            {/* Overview */}
            <h2 className="text-xl md:text-2xl font-semibold text-[#1A1A1A] mb-3">
              Project Overview
            </h2>
            <p className="text-sm md:text-base text-[#666666] leading-relaxed mb-8">
              {project.overview}
            </p>

            {/* Impact */}
            <h2 className="text-xl md:text-2xl font-semibold text-[#1A1A1A] mb-3">
              Key Impact
            </h2>
            <ul className="list-disc list-inside space-y-2 text-sm md:text-base text-[#666666]">
              {project.impact.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>
          </div>

          {/* Sidebar */}
          <aside className="lg:col-span-1 bg-gray-50 p-5 md:p-6 rounded-xl h-fit lg:sticky top-24">
            <h3 className="text-lg md:text-xl font-semibold text-[#1A1A1A] mb-4">
              Tech Stack Used
            </h3>
            <div className="flex flex-wrap gap-2">
              {project.techStack.map((tech) => (
                <span
                  key={tech}
                  className="bg-[#4D4DFF]/10 text-[#4D4DFF] text-xs md:text-sm font-medium px-3 py-1.5 rounded-full"
                >
                  {tech}
                </span>
              ))}
            </div>
          </aside>
        </div>

        {/* Gallery */}
        <div className="mt-16 md:mt-20">
          <h2 className="text-xl md:text-2xl font-semibold text-[#1A1A1A] mb-6">
            Gallery
          </h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-5">
            {project.images.map((img, index) => (
              <div
                key={index}
                className="relative w-full h-56 rounded-lg overflow-hidden shadow-md"
              >
                <Image
                  src={img}
                  alt={`${project.title} screenshot ${index + 1}`}
                  fill
                  className="object-cover"
                />
              </div>
            ))}
          </div>
        </div>
      </main>

      <ContactCTASection />
      <Footer />
    </>
  );
}
